.fadeMe {
  opacity:    0.8; 
  font-weight: bold;
  background: white; 
  width:      58%;
  height:     100%;
  line-height: 750px; 
  z-index:    10;
  top:        0; 
  margin:auto;
  text-align: center;
  vertical-align: middle;
  position:fixed;
}

